export default {
	name: 'UniProlist',
	data() {
		return {
			//数据
			productList: [],
		};
	},
	created() {
		this.changData();
	},
	methods: {
		funUp(){
			this.$emit("getFun",this.loadmore)
		},
		loadmore() {
			console.log(this.$store.state.bol, 111);
			if (this.$store.state.bol == true) {
				//修改状态
				this.loadtext = '加载中...';
				console.log(this.loadtext);
				//获取数据
				//let obj = this.productList;
				// this.productList.push(obj);
				//this.loadtext = '上拉加载更多';
			}
		},
		// 拿数据
		async changData() {
			let [error, res] = await uni.request({
				url: 'http://ceshi3.dishait.cn/api/index_category/data'
			});
			this.productList = res.data.data.data[4].data;
		}
	}
};
